import { useEffect, useState } from "react";
import { useNavigate } from 'react-router-dom';
import axios from "axios";
import Footer from "../Footer/Footer";
import { BiChevronLeft } from "react-icons/bi";
import myStyle from "./my.module.css";

const PaymentList = () => {
  const navigate = useNavigate();
  const [reserves, setReserves] = useState([]);
  const [loading, setLoading] = useState(false);

 
  const [eventData, setEventData] = useState();
  const [selectedDate, setSelectedDate] = useState();
  const [numberOfPeople, setNumberOfPeople] = useState();



  const getReserveData = async () => {
    try {
      setLoading(true);
  
      const result = await axios.get(`/paymentlist`, {
        params: {
          eventData,
          selectedDate,
          numberOfPeople,
        },
      });
  
      const reservesData = result.data.docs.map((reserve, index) => {
        const id = reserve._id ? reserve._id.toString() : null;
  
        if(id && reserve.eventData) {
          return {
            title: reserve.eventData.TITLE,
            eventData: reserve.eventData,
            mainImg: reserve.eventData.MAIN_IMG,
            selectedDate: reserve.selectedDate,
            numberOfPeople: reserve.numberOfPeople,
            id: id,
          };
        } else {
          return null; // _id     .
        }
      }).filter(reserve => reserve !== null);
  
      setReserves(reservesData);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getReserveData();
  }, [eventData, selectedDate, numberOfPeople,]);

  const onDelete = async (reserve) => {
    console.log("  .  reserve id :", reserve.id);

    const reCheck = window.confirm(`${reserve.title}  ?`);
  
    if (reCheck) {
      try {
        await axios.delete(`/paymentlist/delete/${reserve.id}`);
        getReserveData();
      } catch (error) {
        console.log("   : ", error);
      }
    }
  };

  const handleCancel = () => {
    const confirmResult = window.confirm('  ?');
    if (confirmResult) {
      //     
      navigate(-1);
    }
  };


  return (
    <div className={myStyle.paymentlistBox}>
       <div className={myStyle.paymentlistTop}>
      <button onClick={handleCancel}><BiChevronLeft /></button>
        <h2></h2>
      </div>
      <div className={myStyle.paymentlist}>
    {loading ? (
      <h2>...</h2>
    ) : (
      <div>
        {reserves.length === 0 ? (
          <p>   .</p>
        ) : (
          reserves.map((reserve, index) => (
            <div key={index} className={myStyle.paymentlistItem}>
              <img src={reserve.mainImg} alt={`  - ${reserve.title}`}  />
              <div className={myStyle.paymentlistTxt}>
              <p> {reserve.title}</p>
              <p> {new Date(reserve.selectedDate).toLocaleDateString("ko-KR")}</p>
              <p>: {reserve.numberOfPeople}</p>
              </div>
              <button onClick={() => onDelete(reserve)} className={myStyle.paymentlistDeleteBtn}></button>
            </div>
          ))
        )}
      </div>
    )}
  </div>
  <Footer/>
  </div>
  );
};

export default  PaymentList;
