import { useEffect, useState, useCallback  } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import SplashPage from "./SplashPage";
import loginStyle from "./login.module.css";


const LoginHome = () => {
  const publicUrl = process.env.PUBLIC_URL;
  const [user, setUser] = useState(null);
  const [isSplash, setIsSplash] = useState(true)

  const navigate = useNavigate();
  const [errMsg, setErrMsg] = useState("");
  const [formData, setFormData] = useState({
    userId: "",
    userPw: "",
  });
  const { userId, userPw } = formData;

  const handleChange = useCallback((e) => {
    const { name, value } = e.target;
    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));
  }, []);

  const handleLogin = useCallback(
    async (e) => {
      e.preventDefault();

      if (!userId || !userPw) {
        setErrMsg("   ");
        return;
      }

      try {
        const res = await axios.post("/login", formData);
        if (res.data.user) {
          alert(` : ${res.data.user.userId}`);
          navigate("/ex");
        } else {
          throw new Error(" ");
        }
      } catch (err) {
        console.log("  : ", err);
        setErrMsg("  ");
      }
      console.log(" id, pw", formData);
    },
    [formData, navigate]
  );

  useEffect(() => {
    const timeout = setTimeout(() => {
      setIsSplash(false);
    }, 2000);

    return () => clearTimeout(timeout);
  }, []); 

  //   
  useEffect(() => {
    const getUserSession = async () => {
      try {
        const res = await axios.get("/login");
        setUser(res.data.user);
      } catch (err) {
        console.log(" :", err);
      }
    };
    getUserSession();
  }, []);



  return (
    <div className={loginStyle.loginhomeBox}>
      {isSplash && <SplashPage />}
        <h1 className={loginStyle.loginLogo}>
         <img src={`${publicUrl}/images/loginLogo.svg`} alt=" " />
        </h1>
        <div className={loginStyle.loginInputBox}>
        <form onSubmit={handleLogin}>
        <label>
          <input
            type="text"
            placeholder=" "
            name="userId"
            value={userId}
            onChange={handleChange}
          />
        </label>

        <label>
          <input
            type="password"
            placeholder=" "
            name="userPw"
            value={userPw}
            onChange={handleChange}
          />
        {errMsg && <div className={loginStyle.loginErrorTxt}>{errMsg}</div>}
        </label>

        <p className={loginStyle.loginPwTxt}> ?</p>
        
        <p className={loginStyle.loginJoinTxt}>       JOIN NOW    <br />    .</p>
        <button type="submit" className={loginStyle.loginBtn}>
          
        </button>
      </form>
       
        </div>

        <div>
          <Link to="/signup">
            <button className={loginStyle.loginJoinBtn}></button>
          </Link>
        </div>
   </div>

  );
};

export default LoginHome;




