import React, { useState, useEffect } from 'react';
import { useLocation, useParams, useNavigate, Link } from 'react-router-dom';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import moment from "moment";
import searchStyle from "./search.module.css";
import { BiX, BiChevronLeft } from "react-icons/bi";
import axios from "axios";

const Reserve = () => {
  const { title } = useParams();
  const decodedTitle = decodeURIComponent(title);

  const location = useLocation();
  const navigate = useNavigate();

  const [eventData, setEventData] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [numberOfPeople, setNumberOfPeople] = useState(1);
  const [cartItems, setCartItems] = useState([]);

  const handleReserve = async () => {
    try {
      console.log('Data to send:', {
        eventData,
        selectedDate,
        numberOfPeople,
      });

      
  
      const response = await axios.post(`/reserve/${encodeURIComponent(title)}`, {
        eventData,
        selectedDate,
        numberOfPeople,
      });
  
      if (response.status === 200) {
        console.log('Reservation successful!');

        const confirmResult = window.confirm(' ?');

        //   /save  
        if (confirmResult) {
          navigate('/save');
        }
      }
    } catch (error) {
      console.error('Reservation error:', error);

    }
  };

  useEffect(() => {
    console.log('location.state:', location.state);

    // location.state eventData  
    if (location.state && location.state.eventData) {
      setEventData(location.state.eventData);
    }
  }, [decodedTitle, location.state ]);

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };


  const increasePeople = () => {
    setNumberOfPeople(prevCount => prevCount + 1);
  };

  const decreasePeople = () => {
    if (numberOfPeople > 1) {
      setNumberOfPeople(prevCount => prevCount - 1);
    }
  };

  const handleCancel = () => {
      navigate(-1);
  };

  return (
    <div >
      {eventData ? (
        <div className={searchStyle.reserveBox}>
          {/* eventData     */}
          <img
            src={eventData.MAIN_IMG || ''}
            alt={`Image for ${eventData.TITLE || ''}`}
            className={searchStyle.reserveBoxImg}
          />
          <button onClick={handleCancel} className={searchStyle.reserveCancleBtn}><BiChevronLeft /></button>
          <div className={searchStyle.reserveBoxTxt}>
          <p className={searchStyle.reserveBoxCode}>{eventData.CODENAME || '-'}</p>
          <p className={searchStyle.reserveBoxTitle}>{eventData.TITLE || '-'}</p>
          <p className={searchStyle.reserveBoxPlace}>{eventData.PLACE || '-'}</p>
          <div className={searchStyle.reserveBoxFlex}>
          {eventData.DATE ? (
            <>
              <p className={searchStyle.reserveBoxDate}>{eventData.DATE.split('~')[0]} / {eventData.DATE.split('~')[1]}</p>
            </>
          ) : (
            <p>  </p>
          )}
          <p className={searchStyle.reserveBoxFree}>{eventData.IS_FREE || '-'}</p>
          </div>
          <div  className={searchStyle.reserveInfoBox}>
          <h3></h3>
          <ul>
            <li>-       ( )</li>
            <li>-      / </li>
            <li>- ,      / </li>
            <li>-   .     /  </li>
            <li>-     /   </li>
          </ul>
          </div>
          <button onClick={openModal} className={searchStyle.reserveBoxBtn}></button>
        </div>
        </div>
      ) : (
        <p>   .</p>
      )}
      
       {/*  */}
      {isModalOpen && (
        <div className={searchStyle.reserveModalBack}>
        <div className={searchStyle.reserveModal}>
          <div className={searchStyle.reserveModalContent}>
          <div className={searchStyle.reserveModalContentTop}>
          <button onClick={closeModal}><BiX /></button>
          </div>
            <h3> </h3>
            
            <Calendar
              onChange={setSelectedDate}
              value={selectedDate}
              formatDay={(locale, date) => moment(date).format("DD")}
              locale="ko-KR"
               showNavigation={true}
              className={searchStyle.calendarContainer}
              tileClassName={({ date, view }) => {
                //      
                if (view === 'month') {
                  const isCurrentDate = moment(date).isSame(moment(), 'day');
                  const isSelectedDate = moment(date).isSame(selectedDate, 'day');
      
                  return `
                    ${isCurrentDate ? searchStyle.currentDate : ''}
                    ${isSelectedDate ? searchStyle.selectedDate : ''}
                  `;
                }
      
                return '';
              }}
            />
            <h3> </h3>
            <div className={ searchStyle.peopleSelectorFlex}>
            <div className={ searchStyle.peopleSelectorBtn}>
              <button onClick={decreasePeople}>-</button>
              <span>{numberOfPeople}</span>
              <button onClick={increasePeople}>+</button>
            </div>
              <p className={ searchStyle.peopleSelectorTxt}> {numberOfPeople} </p>
            </div>
            <Link
            to={`/payment/${encodeURIComponent(eventData.TITLE)}`}
            state= { {eventData: eventData,  selectedDate: selectedDate,
              numberOfPeople: numberOfPeople,}}
            onClick={() => console.log('Link clicked', eventData.TITLE, eventData)}
            >
            <button className={searchStyle.reservePaymentBtn}></button>
            </Link>
            <button  onClick={handleReserve} className={searchStyle.reserveBtn}></button>
            
          </div>
        </div>
        </div>
      )}
    </div>
  );
};

export default Reserve;

