import { useCallback, useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import loginStyle from "./login.module.css";

const SignUp = () => {
  const navigate = useNavigate();
  const [msg, setMsg] = useState(" ");
  const [formData, setFormData] = useState({
    userId: "",
    userPw: "",
    userName: "",
    userGroup: "",
    userEmail: "",
  });
  const { userId, userPw, userName, userGroup, userEmail } = formData;

    //      
    const handleChange = useCallback((e) => {
      const { name, value } = e.target;
      setFormData((prevFormData) => ({
        ...prevFormData,
        [name]: value,
      }));
    }, []);


    const handleSubmit = async (e) => {
      e.preventDefault();
  
      try {
        const res = await axios.post("/signup", formData);
        if (res.status !== 200) {
          //   throw  setMsg 
          throw new Error(" ");
        }
        alert(" ");
        navigate("/");
      } catch (err) {
        setMsg(err.message);
      }
      console.log("formData :", formData);
    };
  return (
    <div className={loginStyle.singupBox}>
      <div className={loginStyle.singupTop}>
      <button onClick={() => navigate(-1)}></button>
        <h2></h2>
      </div>
      <div  className={loginStyle.singupInputBox}> 
<form onSubmit={handleSubmit}>
  <label>
    <p></p>
    <div  className={loginStyle.singupflexBox}>
    <input
      type="text"
      placeholder=""
      name="userId"
      value={userId}
      onChange={handleChange}
      className={loginStyle.singupInputfirst}
    />
    <button className={loginStyle.singupSecone}> </button>
    </div>
  </label>

  <label>
  <p></p>
    <input
      type="password"
      placeholder=""
      name="userPw"
      value={userPw}
      onChange={handleChange}
      className={loginStyle.singupInput}
    />
  </label>

  <label>
  <p></p>
    <input
      type="text"
      placeholder=""
      name="userName"
      value={userName}
      onChange={handleChange}
      className={loginStyle.singupInput}
    />
  </label>

  <label>
  <p></p>
    <input
      type="email"
      placeholder=""
      name="userEmail"
      value={userEmail}
      onChange={handleChange}
      className={loginStyle.singupInput}
    />
  </label>

  <label>
  <p> </p>
  <div className={loginStyle.singupflexBox}>
    <input
      type="text"
      placeholder=" "
      name="userGroup"
      value={userGroup}
      onChange={handleChange}
      className={loginStyle.singupInputfirst}
    />
    <button className={loginStyle.singupSecone}>  </button>
    </div>
  </label>


  {/* {msg && <div className={loginStyle.singupMsg}>{msg}</div>} */}
  <button type="submit"  className={loginStyle.singupBnt}>
    
  </button>
</form>
</div>
    </div>
  );
};

export default SignUp;