import React from "react";
import {
  BiPencil,
  BiHomeAlt,
  BiSearch,
  BiBasket
} from "react-icons/bi";
import FooterStyle from "./footer.module.css";
import { Link } from "react-router-dom";

const Footer = () => {
  return (
    <div className={FooterStyle.globalMenu}>
      <ul className={FooterStyle.globalMenuItem}>
        <li>
          <Link to="/ex">
            <BiHomeAlt />
            <span></span>
          </Link>
        </li>
        {/* <li>
          <Link to="/ex">
          <BiSearch />
            <span></span>
          </Link>
        </li> */}
        <li>
          <Link to="/save">
          <BiBasket />
            <span></span>
          </Link>
        </li>
        <li>
          <Link to="/my">
            <BiPencil />
            <span></span>
          </Link>
        </li>
      </ul>
    </div>
  );
};

export default Footer;
